import React, { memo, useContext, useState } from "react";

import Weekday from "./components/NewWeekDay";
import Today from "./components/Today";
import WeekScheduleHeading from "./base_components/WeekScheduleHeading";
import { WEEKDAYS } from "./constants/Constants";
import AppContext from "./context/Context";
import SnackBar from "./base_components/Snackbar";
import {
  AppBar,
  Typography,
  Toolbar,
  Tooltip,
  Button,
} from "@material-ui/core";

import { makeStyles } from "@material-ui/core/styles";
import { Save } from "@material-ui/icons";

const getAppBarStyle = makeStyles({
  root: {
    background: "#e9dd71",
    color: "unset",
    // backgroundImage: "linear-gradient(#fff937f0, #fff494d1)",
    // backgroundImage:
    //   "linear-gradient(rgb(227 244 62), rgba(230, 239, 126, 0.86))",
    height: "fit-content",
    padding: "4px 0px",
    justifyContent: "center",
  },
});

const getButtonTypographyStyle = makeStyles((theme) => ({
  root: {
    [theme.breakpoints.down("sm")]: {
      display: "none",
    },
  },
}));

const App = memo(function App({ pending, refreshLocal }) {
  let weekdays = WEEKDAYS;
  const todayDay = weekdays[(new Date().getDay() - 1 + 7) % 7];

  const appbarClassess = getAppBarStyle();
  const buttonTypographyClasses = getButtonTypographyStyle();

  const [alert, setAlert] = useState(() => ({
    open: false,
    type: "",
    message: "",
  }));

  function handleSaveButton() {
    if (refreshLocal()) {
      setAlert({
        open: true,
        type: "success",
        message: "Update Succesfull !!",
      });
    } else {
      setAlert({
        open: true,
        type: "error",
        message: "Update Falied ! Try Later",
      });
    }
  }

  return (
    <React.Fragment>
      <AppBar position="fixed" classes={appbarClassess}>
        <Toolbar>
          {/* <span p={1} flex={1}> */}
          <Typography style={{ flex: 1, padding: "8px" }} variant="h4">
            Diet Plan !!
          </Typography>
          {/* </span> */}

          <Tooltip
            interactive
            arrow
            title={
              pending === false
                ? "No Changes to Save"
                : "Click to save data to get it next time you visit"
            }
          >
            {/* Span to show tooltip for disabled button */}
            <span>
              <Button
                onClick={handleSaveButton}
                disabled={pending === false}
                color="primary"
                variant="contained"
                size="large"
                startIcon={<Save />}
              >
                <Typography classes={buttonTypographyClasses} variant="button">
                  {pending === false ? "Nothing to Save" : "Save Changes"}
                </Typography>
              </Button>
            </span>
          </Tooltip>
        </Toolbar>
      </AppBar>
      <SnackBar
        type={alert.type}
        message={alert.message}
        open={alert.open}
        onClose={() => setAlert((prev) => ({ ...prev, open: false }))}
      />
      {/* <Heading type={1} id="main-heading" value="Diet Plan" /> */}
      <Today day={todayDay} />
      <WeekScheduleHeading />
      {/* <Box m={1}>bb</Box> <Box m={1}>bb</Box> */}
      {weekdays
        .slice(weekdays.indexOf(todayDay), 7)
        .concat(weekdays.slice(0, weekdays.indexOf(todayDay)))
        .map((w) => {
          return <Weekday day={w} key={w} />;
        })}
    </React.Fragment>
  );
}); // memo with default shallow comparator

const AppWithContext = () => {
  const context = useContext(AppContext);
  if (context)
    return (
      <App
        refreshLocal={context.refreshlocalStorage}
        pending={context.pendingUpdates.size > 0}
      ></App>
    );

  return <></>;
};
export default AppWithContext;
