import React, { Suspense } from "react";
import { MEALS } from "../constants/Constants";

import SwipeableViews from "../temp/CustomSwipeable";

import { Typography, Tab, Tabs, Card } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

import BreakfastIcon from "../custom_icons/BreakfastIcon";
import LunchIcon from "../custom_icons/LunchIcon";
import SnacksIcon from "../custom_icons/SnacksIcon";
import DinnerIcon from "../custom_icons/DinnerIcon";
import TodayCard from "../base_components/TodayCard";

// const BreakfastIcon = React.lazy(() => import("../custom_icons/LunchIcon"));

const getTabStyle = makeStyles((theme) => ({
  wrapper: {
    flexDirection: "row",
  },
  root: {
    width: "25%",
    maxWidth: "unset",
    minWidth: "250px",
    [theme.breakpoints.down("xs")]: {
      width: "100%",
      minWidth: "unset",
      borderLeft: "1px solid black",
      borderRight: "1px solid black",
    },
  },
}));

const getTabsStyle = makeStyles((theme) => ({
  flexContainer: {
    width: "100%",
    margin: "auto",
  },
  scrollButtonsDesktop: {
    [theme.breakpoints.down("xs")]: {
      display: "flex",
    },
  },
}));

const getCardStyle = makeStyles((theme) => ({
  root: {
    borderRadius: "40px",
    paddingBottom: "20px",
    width: "80%",
    margin: "auto",
    [theme.breakpoints.down("md")]: {
      width: "90%",
    },
    [theme.breakpoints.down("sm")]: {
      width: "95%",
    },
    [theme.breakpoints.down("xs")]: {
      width: "97%",
    },
  },
}));

export function Today({ day }) {
  const tabClasses = getTabStyle();
  const tabsClasses = getTabsStyle();
  const cardClasses = getCardStyle();

  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  console.log("to");

  return (
    <div style={{ marginTop: "105px" }}>
      {/* <AppBar position="static" style={{ marginTop: "100px" }} color="default"> */}
      <Typography align="center" gutterBottom variant="h3">
        Today Schedule
      </Typography>
      <Card elevation={3} classes={cardClasses}>
        <Tabs
          selectionFollowsFocus={true}
          style={{
            // marginTop: "10px",
            // backgroundImage: "linear-gradient(#edff40, rgb(230 239 126 / 86%))",
            backgroundImage:
              "linear-gradient(#e9dd71, rgba(230, 239, 126, 0.86))",
          }}
          value={value}
          onChange={handleChange}
          classes={tabsClasses}
          // indicatorColor="secondary"
          TabIndicatorProps={{
            style: {
              background: "white",
            },
          }}
          // textColor="primary"
          variant="scrollable"
          scrollButtons="auto"
        >
          {MEALS.map((item, index) => {
            return (
              <Tab
                key={item}
                disableRipple
                style={{
                  // borderLeft: "2px solid grey",
                  backgroundColor: index === value ? "white" : "unset",
                }}
                classes={tabClasses}
                icon={
                  index === 0 ? (
                    // <Suspense
                    //   fallback={
                    //     <Skeleton variant="circle" width={40} height={40} />
                    //   }
                    // >
                    <BreakfastIcon fontSize="large" />
                  ) : // </Suspense>
                  index === 1 ? (
                    <LunchIcon fontSize="large" />
                  ) : index === 2 ? (
                    <SnacksIcon fontSize="large" />
                  ) : (
                    <DinnerIcon fontSize="large" />
                  )
                }
                label={
                  <Typography variant="h6" style={{ marginLeft: "10px" }}>
                    {item}
                  </Typography>
                }
              />
            );
          })}
        </Tabs>
        {/* </AppBar> */}
        <SwipeableViews
          enableMouseEvents={true}
          onChangeIndex={(ind) => {
            setValue(ind);
          }}
          containerStyle={{
            transition: "transform 0.8s cubic-bezier(0.15, 0.3, 0.25, 1) 0s",
          }}
          springConfig={{
            duration: "0.8s",
            easeFunction: "cubic-bezier(0.15, 0.3, 0.25, 1)",
            delay: "0s",
          }}
          index={value}
        >
          {MEALS.map((m, ind) => (
            <TodayCard key={m} day={day} meal={m} value={value} index={ind} />
          ))}
          {/* <TodayCard day={day} meal={MEALS[0]} value={value} index={0} />
          <TodayCard day={day} meal={MEALS[1]} value={value} index={1} />

          <TodayCard day={day} meal={MEALS[2]} value={value} index={2} />

          <TodayCard day={day} meal={MEALS[3]} value={value} index={3} /> */}
        </SwipeableViews>
      </Card>
    </div>
  );
}

export default Today;
