import PropTypes from "prop-types";

import {
  Card,
  CardContent,
  CardMedia,
  Paper,
  Divider,
  Typography,
  Zoom,
} from "@material-ui/core";
import { MEALS } from "../constants/Constants";
import { makeStyles } from "@material-ui/core/styles";

import FastfoodIcon from "@material-ui/icons/Fastfood";
import LocalDiningIcon from "@material-ui/icons/LocalDining";
import HappyIcon from "@material-ui/icons/InsertEmoticon";

import breakfast_img from "../resources/images/a.jpg";
import lunch_img from "../resources/images/lunch1.jpg";
import snacks_img from "../resources/images/snacks12.jpg";
import dinner_img from "../resources/images/dinner1.jpg";
import TodayList from "./List";
import Chip from "../temp/CustomChip";
import { Skeleton } from "@material-ui/lab";
import ImageOutlinedIcon from "@material-ui/icons/ImageOutlined";
import { useState } from "react";

const images = [breakfast_img, lunch_img, snacks_img, dinner_img];

const getChipStyle = makeStyles((theme) => ({
  root: {
    borderColor: "green",
    color: "green",
    margin: "5px",
    [theme.breakpoints.down("sm")]: {
      display: "none",
    },
  },
}));

const getCardStyle = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexDirection: "row",
    height: "350px",
    width: "max(85% , 400px)",

    borderRadius: "30px 30px 30px 30px",
    [theme.breakpoints.down("sm")]: {
      width: "87%",
    },
    [theme.breakpoints.down("xs")]: {
      flexDirection: "column",
      height: "450px",
      width: "87%",
    },
  },
}));

const getPaperWrapperStyle = makeStyles((theme) => ({
  root: {
    display: "flex",
    justifyContent: "center",
    padding: "20px",
    width: "100%",
    [theme.breakpoints.down("xs")]: {
      padding: "20px 5px",
      marginTop: "15px",
    },
  },
}));

const getCardContentStyle = makeStyles({
  root: {
    flex: 5,
    paddingLeft: "5%",
    overflow: "hidden",
  },
});

const getCardMediaStyle = makeStyles((theme) => ({
  root: {
    flex: 4,
    height: "100%",
    overflow: "hidden",
    borderRadius: "30px 70px 70px 30px",
    [theme.breakpoints.down("xs")]: {
      borderRadius: "30px 30px 20px 20px",
      // flex: 3,
    },
  },
}));

const getPaperCardHeadingStyle = makeStyles({
  root: {
    display: "flex",
    justifyContent: "space-between",
  },
});

const getDividerStyle = makeStyles({
  root: { marginBottom: "10px" },
});

export default function TodayCard({ value, index, day, meal }) {
  const chipClasses = getChipStyle();

  const cardClasses = getCardStyle();
  const paperWrapperClasses = getPaperWrapperStyle();
  const cardContentClasses = getCardContentStyle();
  const CardMediaClasses = getCardMediaStyle();
  const paperCardheadingClasses = getPaperCardHeadingStyle();
  const dividerClasses = getDividerStyle();

  const [imageLoad, setImageLoad] = useState("loading");

  console.log("today", day, index, value);
  return value === index ? (
    <Paper classes={paperWrapperClasses}>
      {/* <Box p={2} flex={1}>
      <Typography variant="h4">Diet Plan !!</Typography>
      </Box> */}
      <Card raised elevation={12} classes={cardClasses}>
        <CardMedia
          className={CardMediaClasses.root}
          component="div"
          // image={images[index]}
        >
          {imageLoad !== "fail" ? (
            <Zoom in={true} timeout={1000} style={{ transitionDelay: "300ms" }}>
              <img
                width="100%"
                height="100%"
                alt={MEALS[index].toUpperCase()}
                src={images[index]}
                title={MEALS[index].toUpperCase()}
                onError={(e) => {
                  setImageLoad("fail");
                }}
                onLoad={() => {
                  setImageLoad("success");
                }}
              />
            </Zoom>
          ) : (
            <Zoom in={true} timeout={1000} style={{ transitionDelay: "700ms" }}>
              <ImageOutlinedIcon
                title={"image not available"}
                style={{
                  width: "80%",
                  height: "100%",
                  margin: "auto",
                  display: "block",
                }}
              />
            </Zoom>
          )}
          {imageLoad === "loading" ? (
            <Skeleton component="div" variant="rect" height={"100%"}></Skeleton>
          ) : (
            <></>
          )}
        </CardMedia>

        <CardContent classes={cardContentClasses}>
          <Paper elevation={0} classes={paperCardheadingClasses}>
            <Zoom in={true} timeout={1000} style={{ transitionDelay: "300ms" }}>
              <Typography gutterBottom variant="h5">
                {MEALS[index].toUpperCase()}
              </Typography>
            </Zoom>

            <FastfoodIcon fontSize="large" />
          </Paper>
          <Divider classes={dividerClasses} />
          <Zoom
            mountOnEnter={true}
            unmountOnExit={true}
            in={true}
            timeout={1000}
            style={{ transitionDelay: "300ms" }}
          >
            <Chip
              variant="outlined"
              clickable={true}
              color="primary"
              icon={<LocalDiningIcon />}
              label="Healthy Diet"
            />
          </Zoom>
          <Zoom in={true} timeout={1000} style={{ transitionDelay: "300ms" }}>
            <Chip
              classes={chipClasses}
              color="primary"
              variant="outlined"
              icon={<HappyIcon />}
              label="Happy Meal"
            />
          </Zoom>
          <TodayList type="today" day={day} meal={meal}></TodayList>
        </CardContent>
      </Card>
    </Paper>
  ) : (
    <></>
  );
}

TodayCard.propTypes = {
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};
