import React, { memo } from "react";

import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Divider,
  Paper,
  Typography,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import ExpandedDay from "../base_components/ExpandedDay";

const getAccordionStyle = makeStyles({
  root: {
    width: "100%",
    margin: "auto",
    borderRadius: "9px",
    overflow: "hidden",
  },
  expanded: {
    borderRadius: "39px",

    width: "100%",
  },
});

const getAccordionSummaryStyle = makeStyles({
  root: {
    backgroundColor: "antiquewhite",
    // backgroundImage: `linear-gradient(to right, #00bcd4, #bbe0f042)`,
    backgroundImage: "linear-gradient(to right, #e9dd71, #e7dd87c4)",
  },
});

const getTypographyStyle = makeStyles({
  root: {
    marginLeft: "30px",
  },
});

const getPaperStyle = makeStyles({
  root: {
    marginBottom: "20px",
    background: "transparent",
  },
});

const Weekday = memo(function Weekday({ day }) {
  const [open, setOpen] = React.useState(false);

  const accordionClasses = getAccordionStyle();
  const accordionSummaryClasses = getAccordionSummaryStyle();
  const typographyClasses = getTypographyStyle();
  const paperClasses = getPaperStyle();
  return (
    <Paper elevation={0} id={day} classes={paperClasses}>
      <Accordion
        TransitionProps={{ unmountOnExit: true }}
        square={true}
        classes={accordionClasses}
        expanded={open}
        onChange={(e, open) => {
          setOpen(open);
        }}
      >
        <AccordionSummary
          classes={accordionSummaryClasses}
          expandIcon={<ExpandMoreIcon />}
        >
          <Typography classes={typographyClasses} variant="h4">
            {day[0].toUpperCase() + day.substring(1)}
          </Typography>
        </AccordionSummary>
        <Divider />

        <AccordionDetails>
          <ExpandedDay day={day} />
        </AccordionDetails>
      </Accordion>
    </Paper>
  );
}); //Rerender the day only if any update has happened to any of its props
// schedule props(reference i.e. only shallow compare required)  will be updated if any update has occured for a day

export default Weekday;
