import { makeStyles, Typography } from "@material-ui/core";
import React, { memo } from "react";

const getTypographyStyle = makeStyles({
  root: {
    marginTop: "40px",
  },
});

const WeekScheduleHeading = memo(function WeekScheduleHeading({
  displayModalHandler,
}) {
  const typographyClasses = getTypographyStyle();
  return (
    <Typography
      classes={typographyClasses}
      align="center"
      gutterBottom
      variant="h3"
    >
      Weekly Schedule
    </Typography>
  );
}); // Heading will rerender only if displayhandler changes

export default WeekScheduleHeading;
