import { Box } from "@material-ui/core";
import { Skeleton } from "@material-ui/lab";
// import { Box } from "@material-ui/core";
import React, { Suspense } from "react";

const Chip = React.lazy(() =>
  import("@material-ui/core/Chip").then(async function (m) {
    await new Promise((resolve) => {
      setTimeout(resolve, 3000);
    });
    return m;
  })
);

const CustomChip = React.forwardRef((props, ref) => {
  const { classes, style, ...other } = props;
  // console.log(other);
  return (
    <Box display={"inline-flex"} ref={ref} style={style}>
      <Suspense
        fallback={
          <Skeleton
            classes={classes}
            variant="rect"
            width={90}
            height={20}
          ></Skeleton>
        }
      >
        <Chip classes={classes} style={style} {...other}></Chip>
      </Suspense>
    </Box>
  );
});
export default CustomChip;
