import {
  Avatar,
  Card,
  CardContent,
  Divider,
  Paper,
  Typography,
  Zoom,
} from "@material-ui/core";

import ScheduleIcon from "@material-ui/icons/Schedule";
import BreakfastIcon from "../custom_icons/BreakfastIcon";
import { makeStyles } from "@material-ui/core/styles";
import React, { memo } from "react";
import { MEALS, TIME } from "../constants/Constants";
import LunchIcon from "../custom_icons/LunchIcon";
import SnacksIcon from "../custom_icons/SnacksIcon";
import DinnerIcon from "../custom_icons/DinnerIcon";
import ListComp from "./List";
import Chip from "../temp/CustomChip";
// const BreakfastIcon = React.lazy(() => import("../custom_icons/BreakfastIcon"));

const getCardStyle = makeStyles({
  root: {
    display: "flex",
    flex: 1,
    flexDirection: "row",
    height: "400px",
    minWidth: "min(400px , 87% )",
    margin: "20px",
    // marginLeft: "0px",
    borderRadius: "30px 30px 30px 30px",
    "&:focus-within": {
      height: "440px",
      outline: "unset",
    },
  },
});

const getChipStyle = makeStyles({
  colorPrimary: {
    color: "green",
  },
  root: {
    borderColor: "green",
  },
});

const background = [
  `linear-gradient(to bottom, #ff5722, rgb(222 178 113 / 46%))`,
  `linear-gradient(#8bc34a, rgb(164 215 166 / 40%))`,
  `linear-gradient(#ff9800, rgb(219 212 151 / 41%))`,
  `linear-gradient(#673ab7, rgb(159 131 207 / 45%))`,
];

const getCardPaperStyle = [
  makeStyles({
    root: {
      height: "28%",
      width: "100%",
      display: "flex",
      justifyContent: "flex-end",
      borderRadius: "0px 0px 20px 20px",
      backgroundImage: background[0],
      flexDirection: "column",
    },
  }),
  makeStyles({
    root: {
      height: "90px",
      width: "100%",
      display: "flex",
      justifyContent: "flex-end",
      borderRadius: "0px 0px 20px 20px",
      backgroundImage: background[1],
      flexDirection: "column",
    },
  }),
  makeStyles({
    root: {
      height: "90px",
      width: "100%",
      display: "flex",
      justifyContent: "flex-end",
      borderRadius: "0px 0px 20px 20px",
      backgroundImage: background[2],
      flexDirection: "column",
    },
  }),
  makeStyles({
    root: {
      height: "90px",
      width: "100%",
      display: "flex",
      justifyContent: "flex-end",
      borderRadius: "0px 0px 20px 20px",
      backgroundImage: background[3],
      flexDirection: "column",
    },
  }),
];

const getAvatarStyle = makeStyles({
  root: {
    backgroundColor: "white",
    marginBottom: "-25px",
    height: "61px",
    width: "70px",
  },
});

const getCardContentStyle = makeStyles({
  root: {
    width: "100%",
    flex: 5,
    paddingLeft: "5%",
  },
});

const getIconStyle = makeStyles({
  root: { fontSize: "42px" },
});

const getTypographyStyle = makeStyles({
  root: {
    marginTop: "25px",
  },
});

const getDividerStyle = makeStyles({
  root: {
    marginBottom: "10px",
  },
});

function setCardFocus(e) {
  // Only focus for tab elements
  /*   e.target.closest(".MuiTabs-scroller.MuiTabs-scrollable").scrollLeft =
    e.target.closest('div[role="tab"]').offsetLeft - 20; */
  const scroller = e.target.closest(".MuiTabs-scroller.MuiTabs-scrollable");
  const tab = e.target.closest('[role="tab"]');
  if (scroller.scrollLeft > tab.offsetLeft - 15)
    scroller.scrollLeft = tab.offsetLeft - 15;
  else if (
    scroller.scrollLeft + scroller.offsetWidth <
    tab.offsetLeft + 15 + tab.offsetWidth
  )
    scroller.scrollLeft =
      -scroller.offsetWidth + tab.offsetLeft + tab.offsetWidth + 15;
}

const MealCard = memo(function ({ index = 0, day, meal }) {
  const cardClasses = getCardStyle();
  const chipClasses = getChipStyle();
  const avatarClasses = getAvatarStyle();
  const cardContentClasses = getCardContentStyle();
  const iconClasses = getIconStyle();
  const typographyClasses = getTypographyStyle();
  const dividerClasses = getDividerStyle();

  return (
    <Card
      classes={cardClasses}
      onFocus={setCardFocus}
      tabIndex={1}
      role="tab"
      raised
    >
      <CardContent classes={cardContentClasses}>
        <Paper elevation={0} classes={getCardPaperStyle[index]()}>
          <Avatar classes={avatarClasses}>
            {index === 0 ? (
              // <Suspense
              //   fallback={<Skeleton variant="circle" width={40} height={40} />}
              // >
              <BreakfastIcon classes={iconClasses} fontSize="large" />
            ) : // </Suspense>
            index === 1 ? (
              <LunchIcon classes={iconClasses} fontSize="large" />
            ) : index === 2 ? (
              <SnacksIcon classes={iconClasses} fontSize="large" />
            ) : (
              <DinnerIcon classes={iconClasses} fontSize="large" />
            )}
          </Avatar>
        </Paper>
        <Typography classes={typographyClasses} gutterBottom variant="h5">
          {MEALS[index][0].toUpperCase() + MEALS[index].substr(1).toLowerCase()}
        </Typography>

        <Divider classes={dividerClasses} />
        <Zoom in={true} timeout={1200} style={{ transitionDelay: "300ms" }}>
          <Chip
            classes={chipClasses}
            variant="outlined"
            color="primary"
            icon={<ScheduleIcon />}
            label={TIME[index]}
          />
        </Zoom>

        <ListComp type="weekday" day={day} meal={meal}></ListComp>
      </CardContent>
    </Card>
  );
});

export default MealCard;
