import { Add, Delete } from "@material-ui/icons";
import { memo, useContext, useEffect, useRef } from "react";
import { makeStyles } from "@material-ui/core/styles";

import {
  Button,
  IconButton,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Typography,
  Zoom,
  Tooltip,
} from "@material-ui/core";

import empty_img from "../resources/images/empty1.svg";
import LocalDiningIcon from "@material-ui/icons/LocalDining";
import React from "react";
import AppContext from "../context/Context";

// Convert User Input to sentenceCase
function getSentenceCase(data) {
  return data
    .replace("\n", " ")
    .trim()
    .split(" ")
    .filter((x) => x.trim().length >= 1)
    .map(function (a) {
      return a[0].toUpperCase() + a.substring(1);
    })
    .join(" ");
}

// For Today Cards
const getListItemTypoStyle = makeStyles({
  root: {
    "&:empty::after": {
      // relate to font size!!
      fontStyle: "italic",
      fontFamily: "Courier New,Courier,monospace",
      content: `"Click to Edit"`,
    },
  },
});

const getListItemDeleteIconStyle = makeStyles({
  root: {
    justifyContent: "flex-end",
  },
});

// For Today CardList
const todayCardStyle = {
  getListStyle: makeStyles({
    root: {
      height: "min(230px , 70%)",
      overflowY: "auto",
      width: "100% ",
      "&:empty": {
        height: "65%",
        margin: `0px auto`,
        display: "block",
        backgroundSize: "79px",
        backgroundImage: `url(${empty_img})`,
        backgroundRepeat: "no-repeat",
        backgroundPositionX: "40px",
        backgroundPositionY: "20px",
      },
      "&:empty::after": {
        content: `"No items"`,
        fontStyle: "italic",
        /* font-family: "Courier New", Courier, monospace; */
        position: "absolute",
        fontSize: "24px",
        textAlign: "center",
        /* white-space: pre; */
        top: "calc(101px + 11%)",
        left: "33px",
      },
    },
  }),

  getListItemStyle: makeStyles({
    root: {
      padding: "0px",
      paddingBottom: "8px",
      wordBreak: "break-all",
    },
  }),
  getAddButtonStyle: makeStyles((theme) => ({
    root: {
      marginBottom: "0px",
      padding: "5px",
      marginRight: "10px",
      float: "right",
      height: "fit-content",
      [theme.breakpoints.down("sm")]: {
        marginRight: "2px",
      },
    },
  })),
};

// For WeekdayCards

const weekdayCardStyle = {
  getListItemStyle: makeStyles({
    root: {
      padding: "0px",
      wordBreak: "break-all",
      whiteSpace: "normal",
    },
  }),
  getListStyle: makeStyles({
    root: {
      height: "52%",
      overflowY: "auto",
      width: "100%",
      paddingTop: "2px",

      "&:empty": {
        height: "180px",
        margin: `0px auto`,
        display: "block",
        backgroundSize: "79px",
        backgroundImage: `url(${empty_img})`,
        backgroundRepeat: "no-repeat",
        backgroundPositionX: "40px",
        backgroundPositionY: "20px",
      },
      "&:empty::after": {
        content: `"No items"`,
        fontStyle: "italic",
        /* font-family: "Courier New", Courier, monospace; */
        position: "absolute",
        fontSize: "24px",
        textAlign: "center",
        /* white-space: pre; */
        bottom: "28px",
        left: "30px",
      },
    },
  }),
  getAddButtonStyle: makeStyles({
    root: {
      marginBottom: "0px",
      // marginRight: "40px",
      float: "right",
      padding: "6px",
      height: "fit-content",
    },
  }),
};

const EditableTypoGraphy = memo(
  ({ ind, data, updateItem }) => {
    const listItemTypoClasses = getListItemTypoStyle();
    const ref = useRef(null);

    return (
      <Typography
        ref={ref}
        onKeyPress={(e) => {
          if (e.code === "Enter") {
            // On Enter Press Avoid Defaults
            e.preventDefault();
            e.target.blur();
            updateItem(ind, getSentenceCase(e.target.innerText));
          }
        }}
        contentEditable={true}
        suppressContentEditableWarning={true}
        classes={listItemTypoClasses}
        onBlur={(e) => {
          if (e.target.innerText !== data)
            updateItem(ind, getSentenceCase(e.target.innerText));
        }}
        variant="body1"
      >
        {data}
      </Typography>
    );
  },
  (prev, cur) => prev.data === cur.data && prev.ind === cur.ind
);

const ListComp = memo(
  ({ type = "today", data = [], updateData }) => {
    const listClasses =
      type === "today"
        ? todayCardStyle.getListStyle()
        : weekdayCardStyle.getListStyle();

    const addButtonClasses =
      type === "today"
        ? todayCardStyle.getAddButtonStyle()
        : weekdayCardStyle.getAddButtonStyle();

    const listItemClasses =
      type === "today"
        ? todayCardStyle.getListItemStyle()
        : weekdayCardStyle.getListItemStyle();

    const listItemDeleteIconClasses = getListItemDeleteIconStyle();

    // Ref Variable to set Transition for list item
    const isNewItemAdded = useRef(false);

    useEffect(() => {
      isNewItemAdded.current = false;
    }); // update New itme State after rendering

    function deleteItem(index) {
      if (data.length > index)
        updateData(data.slice(0, index).concat(data.slice(index + 1)));
    }

    function appendItem() {
      updateData([...data, ""]);
    }

    function updateItem(index, item) {
      if (data.length > index) {
        updateData([...data.slice(0, index), item, ...data.slice(index + 1)]);
      }
    }

    function onAddClick(e) {
      isNewItemAdded.current = true;
      appendItem();
    }

    console.log("list ", data);
    return (
      <>
        <Tooltip arrow title="Click to add new food item ">
          {type === "today" ? (
            <Button // Add button for Today Card
              classes={addButtonClasses}
              size="large"
              onClick={onAddClick}
              // variant="contained"
              startIcon={<Add />}
            >
              Item
            </Button>
          ) : (
            // Add Button for weekdayCards

            <IconButton
              classes={addButtonClasses}
              color="primary"
              size="medium"
              onClick={onAddClick}
              // variant="contained"
            >
              <Add />
            </IconButton>
          )}
        </Tooltip>
        <List classes={listClasses}>
          {data.map((row, ind) => (
            <Zoom
              key={ind}
              in={true}
              timeout={isNewItemAdded.current ? 500 : 1200}
            >
              <ListItem classes={listItemClasses}>
                <ListItemIcon>
                  <LocalDiningIcon />
                </ListItemIcon>
                <ListItemText>
                  <EditableTypoGraphy
                    ind={ind}
                    data={row}
                    updateItem={updateItem}
                  />
                </ListItemText>
                <ListItemIcon classes={listItemDeleteIconClasses}>
                  <Tooltip arrow title="Click to delete the food item">
                    <IconButton
                      onClick={() => {
                        deleteItem(ind);
                      }}
                    >
                      <Delete fontSize="small" />
                    </IconButton>
                  </Tooltip>
                </ListItemIcon>
              </ListItem>
            </Zoom>
          ))}
        </List>
      </>
    );
  },
  (prev, cur) => prev.data === cur.data
);

export default function ListCompWithContext({
  type = "today",
  day = "monday",
  meal = "lunch",
}) {
  const context = useContext(AppContext);
  // console.log(type, day, meal, context.data[day][meal]);
  if (context)
    return (
      <ListComp
        updateData={function updateData(items) {
          context.updateData(day, meal, items);
        }}
        type={type}
        data={context.data[day][meal]}
      ></ListComp>
    );

  return <></>;
}
