import { IconButton, Snackbar, Typography } from "@material-ui/core";
// import { Alert } from "@material-ui/lab";
import { Close } from "@material-ui/icons";
import React from "react";
import { Suspense } from "react";
import { makeStyles, useTheme } from "@material-ui/core/styles";
const Alert = React.lazy(() => import("@material-ui/lab/Alert"));

const getAlertStyle = makeStyles((theme) => ({
  root: {
    ...theme.typography.h6,
    wordBreak: "break-all",
  },
  icon: {
    ...theme.typography.h4,
    flexDirection: "column",
    justifyContent: "center",
  },
}));
export default function SnackBar({ open, onClose, type, message }) {
  const th2 = useTheme();
  console.log(th2);
  const alertClasses = getAlertStyle();
  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    onClose();
  };
  return (
    <Snackbar
      style={{ maxWidth: "90%", margin: "auto" }}
      open={open}
      autoHideDuration={40000}
      onClose={handleClose}
    >
      <Suspense fallback={<div> {message}</div>}>
        <Alert
          classes={alertClasses}
          elevation={6}
          variant="filled"
          onClose={handleClose}
          severity={type}
          action={
            <IconButton onClick={handleClose} size="medium" color="inherit">
              <Close />
            </IconButton>
          }
        >
          {message}
        </Alert>
      </Suspense>
    </Snackbar>
  );
}
